import Vue from 'vue'
import Router from 'vue-router'
import Article from '../pages/article/article'
import ArticleList from '../pages/article/children/articleList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/article/a/all'
    }, {
      path: '/article',
      component: Article,
      children: [
        {
          path: 'a/:tag',
          component: ArticleList
        }
      ]
    }
  ],
  linkActiveClass: 'active'
})



