<template>
  <ul class="article-list">
    <li v-for="(item, index) of articleList">
      <router-link to="">
        <article class="">
          <div class="tag">
            {{item.tag}}
          </div>
          <div class="left">
            <h3>{{item.title}}</h3>
            <p>{{item.summary}}</p>
            <p>{{item.time}}</p>
          </div>
          <div class="right">
            <img :src="item.imageThumb" >
          </div>

        </article>
      </router-link>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    articleList: {
      type: Array
      /* default属性在父组件有传参数过来的时候无用，不管传过来的是否为数组 */
    }
  },
  watch: {
    articleList (to, from) {
      console.log(from, to);
    }
  }
}
</script>

<style lang="css">
</style>



