<template>
  <article class="article">
    <img src="../../assets/img/icon.jpg" alt="">
    <div class="top">
      <h6 class="">标签</h6>
      <ul v-if="tags.length" class="tag-wrap">
        <li v-for="(item, index) of tags" class="tag">
          {{item}}
        </li>
      </ul>
    </div>
    <article-list v-if="articleList.length" :article-list="articleList"></article-list>
  </article>
</template>

<script>
import { getArticle, getTag } from '../../service/getData'
import ArticleList from './children/articleList'
export default {
  data () {
    return {
      tags: [],
      articleList: []
    }
  },
  created () {
    this.getArticleData()
    this.getTagData()
  },
  methods: {
    async getArticleData () {
      let res = await getArticle()
      console.log(res.data);
      this.articleList = res.data
    },
    getTagData () {
      getTag().then((res) => {
        this.tags = [...new Set(res.data)]
      })
    }
  },
  components: {
    ArticleList
  }
}
</script>

<style lang="scss" scoped>
@import "../../common/style/common";
.article {
  padding: 0 5%;
  &::before {
    content: ' ';
    display: table;
  }
  .top {
    margin-top: 10px;
    >h6 {
      font-size: 16px;
      font-weight: bold;
    }
    >.tag-wrap {
      font-size: 0;

      >.tag {
        display: inline-block;
        font-size: 14px;
        padding: 3px 6px;
        margin: 4px;
        background: #76dcee;
        &:first-child {
          margin-left: 0;
        }
      }
    }
  }

}
</style>



